body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;

  background-position: center center;
  background-repeat:  no-repeat;
  background-attachment: fixed;
  background-size:  cover;
  background-color: rgb(29, 29, 29);
}

html, body, #root {
  height: 100%;
}

.app {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.content {
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.mask {
  z-index: 1;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  
  opacity: 1;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-timing-function: linear;
       -o-transition-timing-function: linear;
          transition-timing-function: linear;
  background-color: rgb(29, 29, 29);
}

.timer {
  color: #fff;
  font-weight: 400;
  font-size: 164px;
  text-shadow: 0px 1px 2px #000;

  cursor: default;
}

.title input{
  width: 650px;
  padding: 10px 20px;
  background: none;
  border: none;

  color: #fff;
  font-size: 48px;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  text-shadow: 0px 1px 2px #000;

  outline: none;
  -webkit-transition: all 0.4s linear;
  -o-transition: all 0.4s linear;
  transition: all 0.4s linear;
}

.title input:focus,
.title input:hover {
  color: #333;
  text-shadow: none;
  background-color: #fff;
  -webkit-box-shadow: 0px 1px 2px #000;
          box-shadow: 0px 1px 2px #000;
}

.controls {
  margin-top: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.controls .defaultButton {
  margin-left: 5px;
  margin-right: 5px;
  width: 86px;
}

.backgroundControls {
  z-index: 2;
  position: absolute;
  top: 0; right: 0;
}

.backgroundButton {
  color: #fff;
  display: block;
  padding: 15px 25px;
  font-size: 24px;
}

.defaultButton {
  display: block;
  padding: 15px 25px;
  margin-bottom: 1px;
  border: solid 1px #fff;
  background-color: transparent;

  color: #fff;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  text-decoration: none;
  text-shadow: 0px 1px 2px #000;

  -webkit-transition: all 0.2s linear;

  -o-transition: all 0.2s linear;

  transition: all 0.2s linear;
}

.defaultButton:hover {
  color: #333;
  text-shadow: none;
  background-color: #fff;
  -webkit-box-shadow: 0px 1px 2px #000;
          box-shadow: 0px 1px 2px #000;
}

.defaultButton:active {
  margin-bottom: 0;
  margin-top: 1px;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

@media (max-width: 840px) {
  .timer {
    font-size: 124px;
  }
  .title input{
    width: 540px;
    font-size: 40px;
  }
}

@media (max-width: 620px) {
  .timer {
    font-size: 80px;
  }
  .title input{
    width: 340px;
    font-size: 24px;
  }
}

@media (max-width: 460px) {
  .timer {
    font-size: 80px;
  }
  .title input{
    width: 280px;
    font-size: 20px;
  }
}


